.banner
{
    height: 100%;
    position: absolute;
    width: 100%;
    color: white;
    object-fit: contain;   
}
.banner_contents
{
    margin-left: 30px;
    padding-top: 140px;
    height:255px;
}
.banner_title
{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner_description
{
    width: 75rem;
    line-height: 1.3rem;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}
.banner_fadeBottom
{
    height: 17rem;
    background-image:linear-gradient(
        180deg,
        transparent,
        rgba(37,37,37, 0.61),
        #111
    ) ;  
}


.banner_button
{
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius:0.2vw ;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51,51,51,0.5);
    padding-bottom: 0.5rem;
}
.banner_button:hover
{
    color: #111;
    background-color: #e6e6e6;
    transition: all 0.2s;
}